/* CV Upload Component Styles */

/* Custom scrollbar styles for webkit browsers */
.cv-upload-scrollbar::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.cv-upload-scrollbar::-webkit-scrollbar-track {
  background: rgba(45, 45, 45, 0.6);
  border-radius: 3px;
}

.cv-upload-scrollbar::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.cv-upload-scrollbar::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.2);
}

/* File drag and drop animations */
.cv-upload-drag-active {
  animation: cv-upload-pulse 1s infinite;
}

@keyframes cv-upload-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(0, 255, 231, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(0, 255, 231, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(0, 255, 231, 0);
  }
}

/* Progress bar shimmer effect */
.cv-upload-progress-shimmer {
  position: relative;
  overflow: hidden;
}

.cv-upload-progress-shimmer::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.3),
    transparent
  );
  animation: cv-upload-shimmer 1.5s infinite;
}

@keyframes cv-upload-shimmer {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

/* Loading spinner */
.cv-upload-spinner {
  animation: cv-upload-spin 1s linear infinite;
}

@keyframes cv-upload-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Focus states for accessibility */
.cv-upload-focus-ring:focus {
  outline: 2px solid #00ffe7;
  outline-offset: 2px;
}

.cv-upload-focus-ring:focus:not(:focus-visible) {
  outline: none;
}

/* Mobile responsive adjustments */
@media (max-width: 768px) {
  .cv-upload-mobile-stack {
    flex-direction: column;
  }
  
  .cv-upload-mobile-full {
    width: 100% !important;
  }
  
  .cv-upload-mobile-padding {
    padding: 1rem !important;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .cv-upload-glass {
    background: rgba(0, 0, 0, 0.9) !important;
    border-color: #ffffff !important;
  }
  
  .cv-upload-text-primary {
    color: #ffffff !important;
  }
  
  .cv-upload-text-secondary {
    color: #cccccc !important;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .cv-upload-motion * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Print styles */
@media print {
  .cv-upload-no-print {
    display: none !important;
  }
}

/* Utility classes */
.cv-upload-sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.cv-upload-truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.cv-upload-break-words {
  word-wrap: break-word;
  word-break: break-word;
}

/* Error state animations */
.cv-upload-error-shake {
  animation: cv-upload-shake 0.5s ease-in-out;
}

@keyframes cv-upload-shake {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
}

/* Success state animations */
.cv-upload-success-bounce {
  animation: cv-upload-bounce 0.6s ease-in-out;
}

@keyframes cv-upload-bounce {
  0%, 20%, 53%, 80%, 100% {
    transform: translate3d(0, 0, 0);
  }
  40%, 43% {
    transform: translate3d(0, -10px, 0);
  }
  70% {
    transform: translate3d(0, -5px, 0);
  }
  90% {
    transform: translate3d(0, -2px, 0);
  }
}

/* File type icons */
.cv-upload-file-icon::before {
  content: "üìÑ";
  margin-right: 0.5rem;
}

.cv-upload-file-icon.pdf::before {
  content: "üìÑ";
}

.cv-upload-file-icon.error::before {
  content: "‚ùå";
}

.cv-upload-file-icon.success::before {
  content: "‚úÖ";
}

/* Tooltip styles */
.cv-upload-tooltip {
  position: relative;
  display: inline-block;
}

.cv-upload-tooltip::after {
  content: attr(data-tooltip);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.9);
  color: white;
  padding: 0.5rem;
  border-radius: 0.25rem;
  white-space: nowrap;
  font-size: 0.75rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  z-index: 1000;
}

.cv-upload-tooltip:hover::after {
  opacity: 1;
}

/* Dark mode specific adjustments */
@media (prefers-color-scheme: dark) {
  .cv-upload-auto-dark {
    filter: brightness(0.9);
  }
}